import { authHeader } from "../_helpers/auth-header";
import { apiUrl } from "../_helpers/constants";
import { handleResponse } from "../_helpers/response";

export const getAllPost = () => {
  const requestOptions: object = {
    method: "GET",
    headers: authHeader(),
  };
  return fetch(`${apiUrl}/post/v2`, requestOptions).then(handleResponse);
};
export const getSinglePost = (id: number) => {
  const requestOptions: object = {
    method: "GET",
    headers: authHeader(),
  };
  return fetch(`${apiUrl}/post/v2?id=${id}`, requestOptions).then(
    handleResponse
  );
};
