import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Box from "@material-ui/core/Box";
import { makeStyles, Paper, Theme } from "@material-ui/core";
import {
  LibraryMusic,
  QueueMusic,
  MusicNote,
  EmojiSymbols,
  Favorite,
} from "@material-ui/icons";
import { History } from "history";
import ExersiceList from "./Exersice/ExersiceList";
import CourseList from "./Course/CourseList";
import LessonList from "./Lesson/LessonList";
import StepList from "./Step/StepList";

const pages = [
  "/learning/guided",
  "/learning/courses",
  "/learning/lessons",
  "/learning/exersice",
  "/learning/favorites",
];

const pageName = (num: number) => {
  return pages[num];
};
const pageNumber = (name: string) => {
  if (pages.includes(name)) return pages.indexOf(name);
  return 0;
};

interface TabPanelProps {
  children?: React.ReactNode;
  dir?: string;
  index: any;
  value: any;
}
function TabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && <Box p={3}>{children}</Box>}
    </div>
  );
}

function a11yProps(index: any) {
  return {
    id: `full-width-tab-${index}`,
    "aria-controls": `full-width-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    flexGrow: 1,
    width: "100%",
  },
}));

interface Props {
  history: History;
}

export default function ScrollableTabsButtonAuto(props: Props) {
  const classes = useStyles();
  const [value, setValue] = React.useState(
    pageNumber(props.history.location.pathname)
  );

  const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
    setValue(newValue);
    props.history.push(pageName(newValue));
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Paper square>
          <Tabs
            value={value}
            onChange={handleChange}
            variant="scrollable"
            indicatorColor="secondary"
            textColor="secondary"
            scrollButtons="auto"
            aria-label="icon label tabs"
          >
            <Tab icon={<LibraryMusic />} label="GUIDED" {...a11yProps(0)} />
            <Tab icon={<QueueMusic />} label="COURSES" {...a11yProps(1)} />
            <Tab icon={<MusicNote />} label="LESSONS" {...a11yProps(2)} />
            <Tab icon={<EmojiSymbols />} label="EXERSICE" {...a11yProps(3)} />
            <Tab icon={<Favorite />} label="FAVORITES" {...a11yProps(4)} />
          </Tabs>
        </Paper>
      </AppBar>
      <>
        <TabPanel value={value} index={0}>
          <CourseList />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <StepList />
        </TabPanel>
        <TabPanel value={value} index={2}>
          <LessonList />
        </TabPanel>
        <TabPanel value={value} index={3}>
          <ExersiceList />
        </TabPanel>
        <TabPanel value={value} index={4}>
          Item Five
        </TabPanel>
      </>
    </div>
  );
}
