import {
  LOST_FAILURE,
  LOST_REQUEST,
  LOST_SUCCESS,
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAILURE,
  LOGOUT,
  UserActionTypes,
  DefaultUserAction,
} from "../_types/user.types";
import {
  REGISTER_FAILURE,
  REGISTER_REQUEST,
  REGISTER_SUCCESS,
} from "../_types/user.types";

import { getUserLocal } from "../_helpers/constants";

const user = getUserLocal("user");

const initialState: DefaultUserAction = user
  ? { loggedIn: true, loading: false, user }
  : { loggedIn: false, loading: false };

export function authReducer(
  state = initialState,
  action: UserActionTypes
): DefaultUserAction {
  switch (action.type) {
    case LOGIN_REQUEST:
      return { ...state, loading: true };
    case LOGIN_SUCCESS:
      return {
        loggedIn: true,
        loading: false,
        user: action.user,
      };
    case LOGIN_FAILURE:
      return { ...state, loading: false };
    case REGISTER_REQUEST:
      return { loggedIn: false, loading: true };
    case REGISTER_SUCCESS:
      return { ...state, loading: false };
    case REGISTER_FAILURE:
      return { ...state, loading: false };
    case LOST_REQUEST:
      return { loggedIn: false, loading: true };
    case LOST_SUCCESS:
      return { ...state, loading: false };
    case LOST_FAILURE:
      return { ...state, loading: false };
    case LOGOUT:
      return { loggedIn: false, loading: false };
    default:
      return state;
  }
}
