import React from "react";
import { createStyles, makeStyles, Theme } from "@material-ui/core";
import {
  AccountCircle,
  Star,
  Notifications,
  MoreHoriz,
} from "@material-ui/icons/";
import Badge from "@material-ui/core/Badge";
import IconButton from "@material-ui/core/IconButton";
import { AppBarInterface } from "./MainAppBar";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    sectionDesktop: {
      display: "none",
      [theme.breakpoints.up("sm")]: {
        display: "flex",
      },
    },
    sectionMobile: {
      display: "flex",
      [theme.breakpoints.up("sm")]: {
        display: "none",
      },
    },
  })
);

const IconController = (props: AppBarInterface) => {
  const classes = useStyles();
  const { menuId, mobileMenuId } = props.state;
  const profileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    props.dispatch({ type: "profile-anchor", payload: event.currentTarget });
  };
  const mobileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    props.dispatch({ type: "mobile-anchor", payload: event.currentTarget });
  };
  return props.loggedIn ? (
    <React.Fragment>
      <div className={classes.sectionDesktop}>
        <IconButton aria-label="show 4 new mails" color="inherit">
          <Badge badgeContent={4} color="secondary">
            <Star />
          </Badge>
        </IconButton>
        <IconButton aria-label="show 17 new alerts" color="inherit">
          <Badge badgeContent={17} color="secondary">
            <Notifications />
          </Badge>
        </IconButton>
        <IconButton
          edge="end"
          aria-label="account of current user"
          aria-controls={menuId}
          aria-haspopup="true"
          onClick={profileMenuOpen}
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
      </div>
      <div className={classes.sectionMobile}>
        <IconButton
          aria-label="show more"
          aria-controls={mobileMenuId}
          aria-haspopup="true"
          onClick={mobileMenuOpen}
          color="inherit"
        >
          <MoreHoriz />
        </IconButton>
      </div>
    </React.Fragment>
  ) : (
    <IconButton
      edge="end"
      aria-label="account of current user"
      aria-controls={menuId}
      aria-haspopup="true"
      onClick={profileMenuOpen}
      color="inherit"
    >
      <AccountCircle />
    </IconButton>
  );
};
export default IconController;
