import { authHeader } from "../_helpers/auth-header";
import { apiUrl } from "../_helpers/constants";
import { handleResponse } from "../_helpers/response";

export const getAllStep = () => {
  const requestOptions: object = {
    method: "GET",
    headers: authHeader(),
  };
  return fetch(`${apiUrl}/lesson/v2/steps`, requestOptions).then(handleResponse);
};
export const getSingleStep = (id: number) => {
  const requestOptions: object = {
    method: "GET",
    headers: authHeader(),
  };
  return fetch(`${apiUrl}/lesson/v2/steps${id}`, requestOptions).then(
    handleResponse
  );
};
