import React from "react";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";
import { sideLink } from "../../../_helpers/constants";
import { AppBarInterface } from "./MainAppBar";

const MenuRender = (props: AppBarInterface) => {
  const { loggedIn, state, logout, dispatch } = props;
  const { profileMoreAnchor, menuId } = state;
  const profileMenuIsOpen = Boolean(profileMoreAnchor);
  const handleSignOut = () => {
    if (logout) logout();
    handleDrawerClose();
  };
  const handleDrawerClose = () => {
    dispatch({ type: "profile-close" });
  };
  const botLinks = loggedIn ? sideLink.in : sideLink.out;
  return (
    <Menu
      anchorEl={profileMoreAnchor}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={profileMenuIsOpen}
      onClose={handleDrawerClose}
    >
      {botLinks.map((item, index) => (
        <Link
          to={item.to}
          key={"menuitem-" + index}
          onClick={handleDrawerClose}
          component={RouterLink}
          color="secondary"
        >
          <MenuItem onClick={handleDrawerClose}>{item.name}</MenuItem>
        </Link>
      ))}
      {loggedIn && <MenuItem onClick={handleSignOut}>خروج</MenuItem>}
    </Menu>
  );
};
export default MenuRender;
