import { Dispatch } from "react";
import { userProfile } from "../_services/profile.service";
import { SHOW_SNACKBAR } from "../_types/snackbar.types";
import { AppActions } from "../_types/app.types";
import {
  INFO_FAILURE,
  INFO_REQUEST,
  INFO_SUCCESS,
} from "../_types/profile.types";
import { UserInfo } from "../_types/profile.types";
import { snackBarMaker } from "./snackbar.actions";

export const profileRequest = (token: string) => {
  return (dispatch: Dispatch<AppActions>) => {
    dispatch({ type: INFO_REQUEST });
    const userInfo: UserInfo = parseToken(token);
    const id: string = userInfo.userID;
    userProfile(id).then(
      (profile) => {
        dispatch({
          type: INFO_SUCCESS,
          profile: { ...profile, info: userInfo },
        });
      },
      (errorObj: object) => {
        const error: string = errorObj.toString();
        dispatch({ type: INFO_FAILURE, error });
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker(error, "error"),
        });
      }
    );
  };
};

const parseToken = (token: string): UserInfo => {
  const base64Url = token.split(".")[1];
  const base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
  const jsonPayload = decodeURIComponent(
    atob(base64)
      .split("")
      .map(function (c) {
        return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
      })
      .join("")
  );
  const resJson = JSON.parse(jsonPayload);
  const userData = {
    userID: resJson.data.user.id,
    expire: resJson.exp * 1000,
    issued: resJson.iat * 1000,
    before: resJson.nbf * 1000,
  };
  return userData;
};
