export const REQUEST_POSTS = "REQUEST_POSTS";
export const SUCCESS_POSTS = "SUCCESS_POSTS";
export const FAILURE_POSTS = "FAILURE_POSTS";

export interface DefaultPostAction {
  posts?: IPost[];
  error?: string;
  loaded?: boolean;
  nextLoad: number;
}

export interface IPost {
  id: number,
  title: string,
  content: string,
  slug: string,
  excerpt: string,
  date: string,
  author: string,
  featured_img: string,
  categories: string[]
  tags: string[]
}

export interface RequestPostAction {
  type: typeof REQUEST_POSTS;
  loaded?: boolean;
}
export interface SeccessPostAction {
  type: typeof SUCCESS_POSTS;
  loaded?: boolean;
  posts: IPost[];
}
export interface FailurePostAction {
  type: typeof FAILURE_POSTS;
  error: string;
  loaded?: boolean;
}

export type PostActionTypes =
  | RequestPostAction
  | SeccessPostAction
  | FailurePostAction;
