export const INFO_REQUEST = "PROFILE_INFO_REQUEST";
export const INFO_SUCCESS = "PROFILE_INFO_SUCCESS";
export const INFO_FAILURE = "PROFILE_INFO_FAILURE";

export const ADD_REQUEST = "PROFILE_ADD_REQUEST";
export const ADD_SUCCESS = "PROFILE_ADD_SUCCESS";
export const ADD_FAILURE = "PROFILE_ADD_FAILURE";

export const REMOVE_REQUEST = "PROFILE_REMOVE_REQUEST";
export const REMOVE_SUCCESS = "PROFILE_REMOVE_SUCCESS";
export const REMOVE_FAILURE = "PROFILE_REMOVE_FAILURE";

export const SAVE_PROFILE = "SAVE_PROFILE";
export const DELETE_PROFILE = "DELETE_PROFILE";

export interface DefaultProfileAction {
  error?: string;
  loading: boolean;
  profile?: IProfile;
}
export type IProfile = {
  id: string;
  name: string;
  slug: string;
  meta: {
    playtime: Playtime[];
  };
  title: { rendered: string };
  score: { rendered: string };
  favorite: favorite[];
  info?: UserInfo;
};
export type UserInfo = {
  userID: string;
  expire: number;
  issued: number;
  before: number;
};
type Playtime = {
  song: {
    name: string;
    id: number;
    notes: number;
  };
  time: string;
  score: string;
  date: string;
  progress: string;
};
interface favorite {
  id: string;
}

export interface SaveProfileInfoAction {
  type: typeof SAVE_PROFILE;
  userData: any;
}
export interface DeleteProfileInfoAction {
  type: typeof DELETE_PROFILE;
}

export interface ProfileInfoRequestAction {
  type: typeof INFO_REQUEST;
}
export interface ProfileInfoSuccessAction {
  type: typeof INFO_SUCCESS;
  profile: IProfile;
}
export interface ProfileInfoFailureAction {
  type: typeof INFO_FAILURE;
  error: string;
}

export interface ProfileAddRequestAction {
  type: typeof ADD_REQUEST;
}
export interface ProfileAddSuccessAction {
  type: typeof ADD_SUCCESS;
  items: IProfile;
}
export interface ProfileAddFailureAction {
  type: typeof ADD_FAILURE;
  items: IProfile;
}

export interface ProfileRemoveRequestAction {
  type: typeof REMOVE_REQUEST;
  id: number;
}
export interface ProfileRemoveSuccessAction {
  type: typeof REMOVE_SUCCESS;
  id: number;
}
export interface ProfileRemoveFailureAction {
  type: typeof REMOVE_FAILURE;
  error: boolean;
  id: number;
}

export type ProfileActionTypes =
  | SaveProfileInfoAction
  | DeleteProfileInfoAction
  | ProfileInfoRequestAction
  | ProfileInfoSuccessAction
  | ProfileInfoFailureAction
  | ProfileAddRequestAction
  | ProfileAddSuccessAction
  | ProfileAddFailureAction
  | ProfileRemoveRequestAction
  | ProfileRemoveSuccessAction
  | ProfileRemoveFailureAction;
