import { Dispatch } from "react";
import {
  FAILURE_EXERSICES,
  REQUEST_EXERSICES,
  SUCCESS_EXERSICES,
  IExersice,
} from "../_types/exersice.types";
import { getAllExersice } from "../_services/exersice.service";
import { AppState } from "../_store/store.config";
import { SHOW_SNACKBAR } from "../_types/snackbar.types";
import { AppActions } from "../_types/app.types";
import { getTheTime } from "../_helpers/constants";
import { snackBarMaker } from "./snackbar.actions";

export const requestAllExersice = () => {
  return (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const exersiceState = getState().exersices;
    const currentTime = getTheTime();
    const resetTime = currentTime - exersiceState.nextLoad;
    if (exersiceState.loaded && resetTime <= 0) {
      return;
    }
    dispatch({ type: REQUEST_EXERSICES, loaded: false });

    getAllExersice().then(
      (exersices: IExersice[]) => dispatch({ type: SUCCESS_EXERSICES, exersices }),
      (errorObj: object) => {
        const error: string = errorObj.toString();
        dispatch({ type: FAILURE_EXERSICES, error });
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker(error, "error"),
        });
      }
    );
  };
};
