import { TransitionCloseHandler, CloseReason } from "notistack";

export const SHOW_SNACKBAR = "SHOW_SNACKBAR";
export const CLEAR_SNACKBAR = "CLEAR_SNACKBAR";
export const REMOVE_SNACKBAR = "REMOVE_SNACKBAR";

export interface DefaultSnackAction {
  type?: string;
  snackbars?: ISnack[];
}
export type ISnack = {
  message: string;
  key: string;
  dismissed?: boolean;
  options?: SnackOption;
};
interface SnackOption {
  variant?: "error" | "success" | "warning";
  action?: (key: string) => JSX.Element;
  onClose?: (
    event: React.SyntheticEvent<any, Event> | null,
    reason: CloseReason,
    myKey?: string | number
  ) => TransitionCloseHandler;
}
export interface SeccessSnackAction {
  type: typeof SHOW_SNACKBAR;
  snackbar: ISnack;
}
export interface ClearSnackAction {
  type: typeof CLEAR_SNACKBAR;
  dismissAll?: boolean;
  key?: string;
}
export interface RemoveSnackAction {
  type: typeof REMOVE_SNACKBAR;
  key: string;
}

export type SnackActionTypes =
  | SeccessSnackAction
  | ClearSnackAction
  | RemoveSnackAction;
