import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";

import { AppActions } from "../../../_types/app.types";
import { ILesson } from "../../../_types/lesson.types";
import { AppState } from "../../../_store/store.config";
import { requestAllLesson } from "../../../_actions/lesson.actions";
import { LessonCard } from "./LessonCard";
import {
  Chip,
  Grid,
  Typography,
  Container,
  CircularProgress,
} from "@material-ui/core";

interface LessonProps {
  lessons?: ILesson[];
  error?: string;
  loaded?: boolean;
}

type Props = LessonProps & LinkDispatchProps;

class LessonList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllLesson();
  }

  render() {
    const { loaded, lessons, error } = this.props;
    const length = lessons ? lessons.length : 0;
    return (
      <Container maxWidth={false} style={{ position: "relative" }}>
        <Chip
          color="secondary"
          label={"تعداد: " + length}
          style={{ position: "absolute", left: 0, top: 0 }}
        />
        <Typography variant="h3">درس ها</Typography>
        <Grid container justify="center" spacing={2}>
          {loaded ? (
            error ? (
              <Typography component="body">{error}</Typography>
            ) : (
              lessons?.map((lesson: ILesson) => (
                <LessonCard key={lesson.id} lesson={lesson} />
              ))
            )
          ) : (
            <CircularProgress color="secondary" />
          )}
        </Grid>
      </Container>
    );
  }
}

interface LinkDispatchProps {
  requestAllLesson: () => void;
}

const mapStateToProps = (state: AppState): LessonProps => {
  const { error, lessons, loaded } = state.lessons;
  return {
    lessons,
    error,
    loaded,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllLesson: bindActionCreators(requestAllLesson, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(LessonList);
