import React from "react";
import { connect } from "react-redux";
import {
  Avatar,
  CssBaseline,
  TextField,
  Box,
  Button,
  Container,
  Typography,
} from "@material-ui/core";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import * as Yup from "yup";
import { Formik } from "formik";
import { lostRequest } from "../../_actions/user.actions";
import { AppState } from "../../_store/store.config";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { AppActions } from "../../_types/app.types";

interface LostPageProps {
  loading: boolean;
}

type UserLost = {
  user_login: string;
};
type Props = LostPageProps & LinkDispatchProps;

class LostPage extends React.Component<Props> {
  formSubmit = (e: UserLost): void => {
    if (e.user_login) {
      this.props.lostRequest(e);
    }
  };
  render() {
    const { loading } = this.props;
    return (
      <Container component="main" maxWidth="xs" style={{ paddingTop: "10px" }}>
        <CssBaseline />
        <div style={{ textAlign: "center" }}>
          <Avatar style={{ margin: "auto" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Lost Password
          </Typography>
          <Formik
            initialValues={{
              user_login: "",
            }}
            validationSchema={Yup.object().shape({
              user_login: Yup.string()
                .max(255)
                .required("Email or Username is required"),
            })}
            onSubmit={(event: UserLost) => {
              this.formSubmit(event);
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
            }) => (
              <form onSubmit={handleSubmit}>
                <Box mt={3} mb={1}>
                  <Typography
                    align="center"
                    color="textSecondary"
                    variant="body1"
                  >
                    Enter your username / email address
                  </Typography>
                </Box>
                <TextField
                  color="secondary"
                  error={Boolean(touched.user_login && errors.user_login)}
                  fullWidth
                  helperText={touched.user_login && errors.user_login}
                  label="Email Address/Username"
                  margin="normal"
                  name="user_login"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="user_login"
                  variant="outlined"
                />
                <Box my={2}>
                  <Button
                    color="secondary"
                    disabled={loading || isSubmitting}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Sign in
                  </Button>
                </Box>
              </form>
            )}
          </Formik>
        </div>
      </Container>
    );
  }
}
interface LinkDispatchProps {
  lostRequest: (user: object) => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): LostPageProps => {
  const { loading } = state.auth;
  return { loading };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>,
  ownProps: LostPageProps
): LinkDispatchProps => ({
  lostRequest: bindActionCreators(lostRequest, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(LostPage);
