import React from "react";
import { IUser } from "../_types/user.types";
import {
  SettingsApplications,
  AccountBox,
  Home,
  FileCopy,
  Info,
} from "@material-ui/icons";

import fallImg from "../assets/img/melodition.jpg";

export const apiUrl = "https://panel.melodition.com/wp-json";

export function getUserLocal(key: string): IUser {
  const item = localStorage.getItem(key);
  return item !== null ? JSON.parse(item) : undefined;
}
export function localTheme(com: "dark" | "light" | "get"): boolean {
  switch (com) {
    case "dark":
      localStorage.setItem("theme", "dark");
      return false;
    case "light":
      localStorage.setItem("theme", "light");
      return false;
    case "get":
      return localStorage.getItem("theme") === "dark" ? true : false;
    default:
      return false;
  }
}
export function getTheTime(minutes: number = 0): number {
  var d = new Date();
  var t = d.getTime();
  const ms = minutes * 60000;
  return t + ms;
}

export const sideLink = {
  in: [
    { name: "پروفایل", to: "/profile", icon: <SettingsApplications /> },
    { name: "کابری من", to: "/dashboard", icon: <AccountBox /> },
  ],
  out: [
    { name: "ورود", to: "/login", icon: <AccountBox /> },
    { name: "ثبت نام", to: "/register", icon: <AccountBox /> },
  ],
};
export const links = [
  { to: "/", title: "خانه‌", icon: <Home /> },
  { to: "/learning", title: "درس ها‌", icon: <FileCopy /> },
  { to: "/about", title: "درباره‌", icon: <Info /> },
];

export const UniqId = (): string => {
  return getTheTime(Math.random()).toString(36);
};

export const thumbnailsFinder = (
  img: string | { medium: string } | null
): string => {
  if (!img) return fallImg;
  else if (typeof img === "object") return img.medium;
  else if (typeof img === "string") return img;
  else return fallImg;
};
