export const REGISTER_REQUEST = "USER_REGISTER_REQUEST";
export const REGISTER_SUCCESS = "USER_REGISTER_SUCCESS";
export const REGISTER_FAILURE = "USER_REGISTER_FAILURE";

export const LOGIN_REQUEST = "USER_LOGIN_REQUEST";
export const LOGIN_SUCCESS = "USER_LOGIN_SUCCESS";
export const LOGIN_FAILURE = "USER_LOGIN_FAILURE";

export const LOGOUT = "USER_LOGOUT";

export const LOST_REQUEST = "USER_LOST_REQUEST";
export const LOST_SUCCESS = "USER_LOST_SUCCESS";
export const LOST_FAILURE = "USER_LOST_FAILURE";

export interface DefaultUserAction {
  loggedIn: boolean;
  loading: boolean;
  user?: IUser;
  error?: string;
}

export type IRegister = {
  username: string;
  password: string;
  firstname: string;
  lastname: string;
  email: string;
};
export type IUser = {
  user_display_name: string;
  token: string;
  user_email: string;
  user_nicename: string;
};

export interface LoginRequestAction {
  type: typeof LOGIN_REQUEST;
}
export interface LoginSuccessAction {
  type: typeof LOGIN_SUCCESS;
  user: IUser;
}
export interface LoginFailureAction {
  type: typeof LOGIN_FAILURE;
  error: string;
}

export interface RegisterRequestAction {
  type: typeof REGISTER_REQUEST;
}
export interface RegisterSuccessAction {
  type: typeof REGISTER_SUCCESS;
  user: string;
}
export interface RegisterFailureAction {
  type: typeof REGISTER_FAILURE;
  error: string;
}

export interface LogoutAction {
  type: typeof LOGOUT;
  loggedIn: boolean;
}

export interface LostRequestAction {
  type: typeof LOST_REQUEST;
  username: object;
}
export interface LostSuccessAction {
  type: typeof LOST_SUCCESS;
  message: string;
}
export interface LostFailureAction {
  type: typeof LOST_FAILURE;
  error: string;
}

export type UserActionTypes =
  | RegisterRequestAction
  | RegisterSuccessAction
  | RegisterFailureAction
  | LostRequestAction
  | LostSuccessAction
  | LostFailureAction
  | LogoutAction
  | LoginRequestAction
  | LoginSuccessAction
  | LoginFailureAction;
