import {
  userLogin,
  userLogout,
  userLost,
  userRegister,
} from "../_services/user.service";
import {
  LOGIN_FAILURE,
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGOUT,
  REGISTER_FAILURE,
  REGISTER_REQUEST,
  REGISTER_SUCCESS,
  LOST_REQUEST,
  LOST_SUCCESS,
  LOST_FAILURE,
  IRegister,
  IUser,
} from "../_types/user.types";
import { Dispatch } from "react";
import { AppActions } from "../_types/app.types";
import { history } from "../_helpers/history";
import { SHOW_SNACKBAR } from "../_types/snackbar.types";
import { snackBarMaker } from "./snackbar.actions";

export const loginRequest = (user: object) => (
  dispatch: Dispatch<AppActions>
) => {
  dispatch({ type: LOGIN_REQUEST });

  userLogin(user).then(
    (user: IUser) => {
      dispatch({ type: LOGIN_SUCCESS, user });
      history.push("/profile");
      dispatch({
        type: SHOW_SNACKBAR,
        snackbar: snackBarMaker(
          `${user.user_display_name} logged in successfully.`,
          "success"
        ),
      });
    },
    (errorObj: object) => {
      const error: string = errorObj.toString();
      dispatch({ type: LOGIN_FAILURE, error });
      dispatch({
        type: SHOW_SNACKBAR,
        snackbar: snackBarMaker(error, "error"),
      });
    }
  );
};

export const logoutRequest = () => {
  userLogout();
  return { type: LOGOUT };
};

export const registerRequest = (user: IRegister) => {
  return (dispatch: Dispatch<AppActions>) => {
    dispatch({ type: REGISTER_REQUEST });

    userRegister(user).then(
      (user) => {
        dispatch({ type: REGISTER_SUCCESS, user });
        history.push("/login");
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker("Registration successful", "success"),
        });
      },
      (errorObj: object) => {
        const error: string = errorObj.toString();
        dispatch({ type: REGISTER_FAILURE, error });
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker(error, "error"),
        });
      }
    );
  };
};

export const lostRequest = (username: object) => {
  return (dispatch: Dispatch<AppActions>) => {
    dispatch({ type: LOST_REQUEST, username });

    userLost(username).then(
      (user) => {
        const message: string = user.message.toString();
        dispatch({ type: LOST_SUCCESS, message });
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker(message, "success"),
        });
      },
      (errorObj: object) => {
        const error: string = errorObj.toString();
        dispatch({ type: LOST_FAILURE, error });
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker(error, "error"),
        });
      }
    );
  };
};
