import { Dispatch } from "react";
import {
  FAILURE_STEPS,
  REQUEST_STEPS,
  SUCCESS_STEPS,
  IStep,
} from "../_types/step.types";
import { getAllStep } from "../_services/step.service";
import { AppState } from "../_store/store.config";
import { SHOW_SNACKBAR } from "../_types/snackbar.types";
import { AppActions } from "../_types/app.types";
import { getTheTime } from "../_helpers/constants";
import { snackBarMaker } from "./snackbar.actions";

export const requestAllStep = () => {
  return (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const stepState = getState().steps;
    const currentTime = getTheTime();
    const resetTime = currentTime - stepState.nextLoad;
    if (stepState.loaded && resetTime <= 0) {
      return;
    }
    dispatch({ type: REQUEST_STEPS, loaded: false });

    getAllStep().then(
      (steps: IStep[]) => dispatch({ type: SUCCESS_STEPS, steps }),
      (errorObj: object) => {
        const error: string = errorObj.toString();
        dispatch({ type: FAILURE_STEPS, error });
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker(error, "error"),
        });
      }
    );
  };
};
