import React, { useState } from "react";
import { Button } from "@material-ui/core";
import { store } from "../../_store/store.config";
import { SHOW_SNACKBAR, CLEAR_SNACKBAR } from "../../_types/snackbar.types";
import { snackBarMaker } from "../../_actions/snackbar.actions";

interface AboutProps {}

const About: React.FC<AboutProps> = () => {
  const [input, setInput] = useState<string>("");

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>): void => {
    const { value } = e.target;
    setInput(value);
  };
  return (
    <>
      <h1>About</h1>
      <p>
        {" "}
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Atque ipsum
        praesentium rem asperiores fuga illo et, architecto doloremque nulla.
        Magni perspiciatis praesentium facere facilis, ullam officiis saepe sunt
        amet nulla?
      </p>
      <input type="text" onChange={(e) => handleChange(e)} value={input} />
      <Button
        onClick={() =>
          store.dispatch({
            type: SHOW_SNACKBAR,
            snackbar: snackBarMaker(input, "success"),
          })
        }
      >
        Snack
      </Button>
      <Button
        onClick={() =>
          store.dispatch({
            type: CLEAR_SNACKBAR,
            dismissAll: true,
          })
        }
      >
        Clear
      </Button>
    </>
  );
};
export default About;
