import {
  DefaultSnackAction,
  CLEAR_SNACKBAR,
  SHOW_SNACKBAR,
  SnackActionTypes,
  REMOVE_SNACKBAR,
} from "../_types/snackbar.types";

const snackbarReducerDefaultState: DefaultSnackAction = {
  snackbars: [],
};

export const snackbarReducer = (
  state = snackbarReducerDefaultState,
  action: SnackActionTypes
): DefaultSnackAction => {
  switch (action.type) {
    case SHOW_SNACKBAR:
      return {
        ...state,
        snackbars: [
          ...state.snackbars,
          {
            ...action.snackbar,
          },
        ],
      };
    case CLEAR_SNACKBAR:
      return {
        ...state,
        snackbars: state.snackbars?.map((snackbar) =>
          action.dismissAll || snackbar.key === action.key
            ? { ...snackbar, dismissed: true }
            : { ...snackbar }
        ),
      };
    case REMOVE_SNACKBAR:
      return {
        ...state,
        snackbars: state.snackbars?.filter(
          (snackbar) => snackbar.key !== action.key
        ),
      };
    default:
      return state;
  }
};
