import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";

import { AppActions } from "../../../_types/app.types";
import { IExersice } from "../../../_types/exersice.types";
import { AppState } from "../../../_store/store.config";
import { requestAllExersice } from "../../../_actions/exersice.actions";
import { ExersiceCard } from "./ExersiceCard";
import {
  Chip,
  Grid,
  Typography,
  Container,
  CircularProgress,
} from "@material-ui/core";

interface ExersiceProps {
  exersices?: IExersice[];
  error?: string;
  loaded?: boolean;
}

type Props = ExersiceProps & LinkDispatchProps;

class ExersiceList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllExersice();
  }

  render() {
    const { loaded, exersices, error } = this.props;
    const length = exersices ? exersices.length : 0;
    return (
      <Container maxWidth={false} style={{ position: "relative" }}>
        <Chip
          color="secondary"
          label={"تعداد: " + length}
          style={{ position: "absolute", left: 0, top: 0 }}
        />
        <Typography variant="h3">تمرین ها</Typography>
        <Grid container justify="center" spacing={2}>
          {loaded ? (
            error ? (
              <Typography component="body">{error}</Typography>
            ) : (
              exersices?.map((exersice: IExersice) => (
                <ExersiceCard key={exersice.id} exersice={exersice} />
              ))
            )
          ) : (
            <CircularProgress color="secondary" />
          )}
        </Grid>
      </Container>
    );
  }
}

interface LinkDispatchProps {
  requestAllExersice: () => void;
}

const mapStateToProps = (state: AppState): ExersiceProps => {
  const { error, exersices, loaded } = state.exersices;
  return {
    exersices,
    error,
    loaded,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllExersice: bindActionCreators(requestAllExersice, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(ExersiceList);
