import { authHeader } from "../_helpers/auth-header";
import { apiUrl } from "../_helpers/constants";
import { handleResponse } from "../_helpers/response";

export const getAllCourse = () => {
  const requestOptions: object = {
    method: "GET",
    headers: authHeader(),
  };
  return fetch(`${apiUrl}/lesson/v2/course`, requestOptions).then(handleResponse);
};
export const getSingleCourse = (id: number) => {
  const requestOptions: object = {
    method: "GET",
    headers: authHeader(),
  };
  return fetch(`${apiUrl}/lesson/v2/course?id=${id}`, requestOptions).then(
    handleResponse
  );
};
