import React from "react";
import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";
import { Badge, IconButton, Menu, MenuItem } from "@material-ui/core";
import { AccountCircle, Stars, Notifications } from "@material-ui/icons";
import { AppBarInterface } from "./MainAppBar";

const MobileMenu = (props: AppBarInterface) => {
  const { mobileMenuId, mobileMoreAnchor } = props.state;
  const mobileMenuIsOpen = Boolean(mobileMoreAnchor);
  const handleProfileMenu = (
    event: React.MouseEvent<HTMLLIElement, MouseEvent>
  ) => {
    props.dispatch({ type: "profile-anchor", payload: event.currentTarget });
  };
  const handleMobileMenu = () => {
    props.dispatch({ type: "mobile-close" });
  };
  const handleSignOut = () => {
    handleMobileMenu();
    if (props.logout) props.logout();
  };
  return props.loggedIn ? (
    <Menu
      anchorEl={mobileMoreAnchor}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={mobileMenuIsOpen}
      onClose={handleMobileMenu}
    >
      <MenuItem>
        <IconButton aria-label="show 4 new mails" color="inherit">
          <Badge badgeContent={4} color="secondary">
            <Stars />
          </Badge>
        </IconButton>
        <p>Messages</p>
      </MenuItem>
      <MenuItem>
        <IconButton aria-label="show 11 new alerts" color="inherit">
          <Badge badgeContent={11} color="secondary">
            <Notifications />
          </Badge>
        </IconButton>
        <p>Notification</p>
      </MenuItem>
      <MenuItem onClick={handleProfileMenu}>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>Account</p>
      </MenuItem>
      <MenuItem onClick={handleSignOut}>
        <IconButton
          aria-label="account of current user"
          aria-controls="primary-search-account-menu"
          aria-haspopup="true"
          color="inherit"
        >
          <AccountCircle />
        </IconButton>
        <p>Sign Out</p>
      </MenuItem>
    </Menu>
  ) : (
    <Menu
      anchorEl={mobileMoreAnchor}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={mobileMenuIsOpen}
      onClose={handleMobileMenu}
    >
      <Link
        to={{ pathname: "/signin" }}
        component={RouterLink}
        color="secondary"
      >
        <MenuItem onClick={handleMobileMenu}>
          <IconButton
            aria-label="account of current user"
            aria-controls="primary-search-account-menu"
            aria-haspopup="true"
            color="inherit"
          >
            <AccountCircle />
          </IconButton>
          <p>ورود</p>
        </MenuItem>
      </Link>
      <Link to="/singup" component={RouterLink} color="secondary">
        <MenuItem onClick={handleMobileMenu}>
          <IconButton
            aria-label="account of current user"
            aria-controls="primary-search-account-menu"
            aria-haspopup="true"
            color="inherit"
          >
            <AccountCircle />
          </IconButton>
          <p>ثبت نام</p>
        </MenuItem>
      </Link>
    </Menu>
  );
};
export default MobileMenu;
