import React from "react";
import clsx from "clsx";
import PropTypes from "prop-types";
import {
  Box,
  Card,
  CardHeader,
  Chip,
  Divider,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableFooter,
  TableRow,
  makeStyles,
  Link,
  useTheme,
  IconButton,
  TablePagination,
} from "@material-ui/core";
import {
  KeyboardArrowLeft,
  KeyboardArrowRight,
  FirstPage,
  LastPage,
} from "@material-ui/icons";
import { UniqId } from "../../../_helpers/constants";
import { Link as RouterLink } from "react-router-dom";

const useStyles1 = makeStyles((theme) => ({
  root: {
    flexShrink: 0,
    marginLeft: theme.spacing(2.5),
  },
}));

function TablePaginationActions(props) {
  const classes = useStyles1();
  const theme = useTheme();
  const { count, page, rowsPerPage, onChangePage } = props;
  const handleFirstPageButtonClick = (event) => {
    onChangePage(event, 0);
  };

  const handleBackButtonClick = (event) => {
    onChangePage(event, page - 1);
  };

  const handleNextButtonClick = (event) => {
    onChangePage(event, page + 1);
  };

  const handleLastPageButtonClick = (event) => {
    onChangePage(event, Math.max(0, Math.ceil(count / rowsPerPage) - 1));
  };

  return (
    <div className={classes.root}>
      <IconButton
        onClick={handleFirstPageButtonClick}
        disabled={page === 0}
        aria-label="first page"
      >
        {theme.direction === "rtl" ? <LastPage /> : <FirstPage />}
      </IconButton>
      <IconButton
        onClick={handleBackButtonClick}
        disabled={page === 0}
        aria-label="previous page"
      >
        {theme.direction === "rtl" ? (
          <KeyboardArrowRight />
        ) : (
          <KeyboardArrowLeft />
        )}
      </IconButton>
      <IconButton
        onClick={handleNextButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="next page"
      >
        {theme.direction === "rtl" ? (
          <KeyboardArrowLeft />
        ) : (
          <KeyboardArrowRight />
        )}
      </IconButton>
      <IconButton
        onClick={handleLastPageButtonClick}
        disabled={page >= Math.ceil(count / rowsPerPage) - 1}
        aria-label="last page"
      >
        {theme.direction === "rtl" ? <FirstPage /> : <LastPage />}
      </IconButton>
    </div>
  );
}

TablePaginationActions.propTypes = {
  count: PropTypes.number.isRequired,
  onChangePage: PropTypes.func.isRequired,
  page: PropTypes.number.isRequired,
  rowsPerPage: PropTypes.number.isRequired,
};

const useStyles2 = makeStyles((theme) => ({
  table: {
    minWidth: 300,
  },
  toolbar: {
    "& div": {
      flexFlow: "wrap",
      justifyContent: "flex-end",
      [theme.breakpoints.only("xs")]: {
        justifyContent: "space-around",
      },
    },
  },
}));

export default function LatestSongs({ res }) {
  const classes = useStyles2();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const emptyRows =
    rowsPerPage - Math.min(rowsPerPage, res.length - page * rowsPerPage);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };
  const pagination = {
    lower: [{ label: "همه", value: -1 }],
    low: [
      { label: "۵", value: 5 },
      { label: "همه", value: -1 },
    ],
    average: [
      { label: "۵", value: 5 },
      { label: "۱۰", value: 10 },
      { label: "همه", value: -1 },
    ],
    high: [
      { label: "۵", value: 5 },
      { label: "۱۰", value: 10 },
      { label: "۲۵", value: 25 },
      { label: "همه", value: -1 },
    ],
    higher: [
      { label: "۵", value: 5 },
      { label: "۱۰", value: 10 },
      { label: "۲۵", value: 25 },
      { label: "۵۰", value: 50 },
      { label: "همه", value: -1 },
    ],
  };
  const sel =
    res.length > 500
      ? "higher"
      : res.length >= 100
      ? "high"
      : res.length >= 30
      ? "average"
      : res.length > 5
      ? "low"
      : "lower";

  // moment.loadPersian({ usePersianDigits: true });

  const dateChanger = (d, t) => {
    let date, time;
    if (d) date = d;
    if (t) time = t;
    return date ? date : time;
  };
  return (
    <Card className={clsx(classes.root)}>
      <CardHeader title="آخرین آهنگ ها" />
      <Divider />
      <Box>
        <Table className={classes.table} aria-label="custom pagination table">
          <TableHead>
            <TableRow>
              <TableCell>نام آهنگ</TableCell>
              <TableCell>ساعت</TableCell>
              <TableCell>تاریخ</TableCell>
              <TableCell>دقت</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {(rowsPerPage > 0
              ? res.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
              : res
            ).map((row) => (
              <TableRow key={UniqId()}>
                {row.song.id !== 0 ? (
                  <TableCell>
                    <Link
                      to={`/learning/lesson=${row.song.id}`}
                      color="secondary"
                      component={RouterLink}
                    >
                      {row.song.name}
                    </Link>
                  </TableCell>
                ) : (
                  <TableCell>
                    <Link to="/course" color="secondary" component={RouterLink}>
                      {row.song.name}
                    </Link>
                  </TableCell>
                )}
                <TableCell>{dateChanger(false, row.time)}</TableCell>
                <TableCell>{dateChanger(row.date)}</TableCell>
                <TableCell>
                  <Chip color="secondary" label={row.progress} size="small" />
                </TableCell>
              </TableRow>
            ))}
            {emptyRows > 0 && (
              <TableRow style={{ height: 57 * emptyRows }}>
                <TableCell colSpan={6} />
              </TableRow>
            )}
          </TableBody>
          <TableFooter>
            <TableRow>
              <TablePagination
                className={classes.toolbar}
                rowsPerPageOptions={pagination[sel]}
                colSpan={4}
                count={res.length}
                rowsPerPage={rowsPerPage}
                page={page}
                SelectProps={{
                  inputProps: { "aria-label": "row per page" },
                  native: true,
                }}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
                ActionsComponent={TablePaginationActions}
              />
            </TableRow>
          </TableFooter>
        </Table>
      </Box>
    </Card>
  );
}
