import { getTheTime } from "../_helpers/constants";
import {
  FavoriteActionTypes,
  REQUEST_FAVORITES,
  SUCCESS_FAVORITES,
  FAILURE_FAVORITES,
  DefaultFavoriteAction,
} from "../_types/favorite.types";

const favoriteReducerDefaultState: DefaultFavoriteAction = {
  loaded: false,
  nextLoad: getTheTime(),
};

export const favoriteReducer = (
  state = favoriteReducerDefaultState,
  action: FavoriteActionTypes
): DefaultFavoriteAction => {
  switch (action.type) {
    case REQUEST_FAVORITES:
      return state;
    case SUCCESS_FAVORITES:
      return {
        favorites: action.favorites,
        nextLoad: getTheTime(2),
        loaded: true,
      };
    case FAILURE_FAVORITES:
      return {
        error: action.error,
        nextLoad: getTheTime(),
        loaded: true,
      };
    default:
      return state;
  }
};
