import React from "react";
import { Link as RouterLink } from "react-router-dom";
import { connect } from "react-redux";
import * as Yup from "yup";

import { registerRequest } from "../../_actions/user.actions";
import { Formik } from "formik";
import { IRegister } from "../../_types/user.types";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { AppActions } from "../../_types/app.types";
import { AppState } from "../../_store/store.config";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import {
  Avatar,
  CssBaseline,
  Box,
  Button,
  Checkbox,
  Container,
  FormHelperText,
  Link,
  TextField,
  Typography,
} from "@material-ui/core";

interface RegisterPageProps {
  loading: boolean;
}

type Props = RegisterPageProps & LinkDispatchProps;

class RegisterPage extends React.Component<Props> {
  formSubmit(e: IRegister) {
    if (e.firstname && e.lastname && e.username && e.email && e.password) {
      this.props.registerRequest({
        firstname: e.firstname,
        lastname: e.lastname,
        username: e.username,
        email: e.email,
        password: e.password,
      });
    }
  }

  render() {
    const { loading } = this.props;
    return (
      <Container component="main" maxWidth="xs" style={{ paddingTop: "10px" }}>
        <CssBaseline />
        <div style={{ textAlign: "center" }}>
          <Avatar style={{ margin: "auto" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign Up
          </Typography>
          <Formik
            initialValues={{
              email: "",
              firstname: "",
              lastname: "",
              username: "",
              password: "",
              confirm_password: "",
              policy: false,
            }}
            validationSchema={Yup.object().shape({
              email: Yup.string()
                .email("Must be a valid email")
                .max(255)
                .required("Email is required"),
              firstname: Yup.string()
                .max(255)
                .required("First name is required"),
              lastname: Yup.string().max(255).required("Last name is required"),
              username: Yup.string().max(255).required("User name is required"),
              password: Yup.string().max(255).required("Password is required"),
              confirm_password: Yup.string().oneOf(
                [Yup.ref("password"), ""],
                "Both password need to be the same"
              ),
              policy: Yup.boolean().oneOf([true], "This field must be checked"),
            })}
            onSubmit={(event) => {
              this.formSubmit(event);
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
              values,
            }) => (
              <form onSubmit={handleSubmit}>
                <Typography
                  color="textSecondary"
                  align="center"
                  gutterBottom
                  variant="body2"
                >
                  Use your email to create new account{" "}
                </Typography>
                <TextField
                  color="secondary"
                  error={Boolean(touched.firstname && errors.firstname)}
                  fullWidth
                  helperText={touched.firstname && errors.firstname}
                  label="نام"
                  margin="normal"
                  name="firstname"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.firstname}
                  variant="outlined"
                />
                <TextField
                  color="secondary"
                  error={Boolean(touched.lastname && errors.lastname)}
                  fullWidth
                  helperText={touched.lastname && errors.lastname}
                  label="نام خانوادگی"
                  margin="normal"
                  name="lastname"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.lastname}
                  variant="outlined"
                />
                <TextField
                  color="secondary"
                  error={Boolean(touched.username && errors.username)}
                  fullWidth
                  helperText={touched.username && errors.username}
                  label="نام کاربری"
                  margin="normal"
                  name="username"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  value={values.username}
                  variant="outlined"
                />
                <TextField
                  color="secondary"
                  error={Boolean(touched.email && errors.email)}
                  fullWidth
                  helperText={touched.email && errors.email}
                  label="آدرس ایمیل"
                  margin="normal"
                  name="email"
                  dir="ltr"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="email"
                  value={values.email}
                  variant="outlined"
                />
                <TextField
                  color="secondary"
                  error={Boolean(touched.password && errors.password)}
                  fullWidth
                  helperText={touched.password && errors.password}
                  label="پسورد"
                  margin="normal"
                  name="password"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="password"
                  value={values.password}
                  variant="outlined"
                />
                <TextField
                  color="secondary"
                  error={Boolean(
                    touched.confirm_password && errors.confirm_password
                  )}
                  fullWidth
                  helperText={
                    touched.confirm_password && errors.confirm_password
                  }
                  label="تائید پسورد"
                  margin="normal"
                  name="confirm_password"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="password"
                  variant="outlined"
                />
                <Box alignItems="center" display="flex" ml={-1}>
                  <Checkbox
                    checked={values.policy}
                    name="policy"
                    onChange={handleChange}
                  />
                  <Typography color="textSecondary" variant="body1">
                    I have read the{" "}
                    <Link
                      color="secondary"
                      component={RouterLink}
                      to="#"
                      underline="always"
                      variant="body1"
                    >
                      Terms and Conditions{" "}
                    </Link>
                  </Typography>
                </Box>
                {Boolean(touched.policy && errors.policy) && (
                  <FormHelperText error>{errors.policy}</FormHelperText>
                )}
                <Box my={2}>
                  <Button
                    color="secondary"
                    disabled={loading}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Sign up now{" "}
                  </Button>
                </Box>
                <Typography
                  align="center"
                  color="textSecondary"
                  variant="body2"
                >
                  Have an account?{" "}
                  <Link
                    color="secondary"
                    component={RouterLink}
                    to="/login"
                    variant="body2"
                  >
                    Sign in{" "}
                  </Link>
                </Typography>
              </form>
            )}
          </Formik>
        </div>
      </Container>
    );
  }
}

interface LinkDispatchProps {
  registerRequest: (user: IRegister) => void;
}

const mapStateToProps = (
  state: AppState,
  ownProps: Props
): RegisterPageProps => {
  const { loading } = state.auth;
  return { loading };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>,
  ownProps: RegisterPageProps
): LinkDispatchProps => ({
  registerRequest: bindActionCreators(registerRequest, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(RegisterPage);
