import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";

import { AppActions } from "../../_types/app.types";
import { DefaultPostAction, IPost } from "../../_types/post.types";
import { AppState } from "../../_store/store.config";
import { requestAllPost } from "../../_actions/post.actions";
import { PostCard } from "./PostCard";

type Props = DefaultPostAction & LinkDispatchProps;

export class PostList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllPost();
  }

  render() {
    const { loaded, posts, error } = this.props;
    return (
      <div>
        <h1>My Posts</h1>
        {loaded ? (
          error ? (
            <div>{error}</div>
          ) : (
            <div
              style={{
                display: "flex",
                justifyContent: "space-around",
                flexWrap: "wrap",
              }}
            >
              {posts?.map((post: IPost) => (
                <PostCard key={post.id} post={post} />
              ))}
            </div>
          )
        ) : (
          <div>Loading...</div>
        )}
      </div>
    );
  }
}

interface LinkDispatchProps {
  requestAllPost: () => void;
}

const mapStateToProps = (state: AppState): DefaultPostAction => ({
  ...state.posts,
});

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllPost: bindActionCreators(requestAllPost, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(PostList);
