import { getTheTime } from "../_helpers/constants";
import {
  LessonActionTypes,
  REQUEST_LESSONS,
  SUCCESS_LESSONS,
  FAILURE_LESSONS,
  DefaultLessonAction,
} from "../_types/lesson.types";

const lessonReducerDefaultState: DefaultLessonAction = {
  loaded: false,
  nextLoad: getTheTime(),
};

export const lessonReducer = (
  state = lessonReducerDefaultState,
  action: LessonActionTypes
): DefaultLessonAction => {
  switch (action.type) {
    case REQUEST_LESSONS:
      return state;
    case SUCCESS_LESSONS:
      return {
        lessons: action.lessons,
        nextLoad: getTheTime(2),
        loaded: true,
      };
    case FAILURE_LESSONS:
      return {
        error: action.error,
        nextLoad: getTheTime(),
        loaded: true,
      };
    default:
      return state;
  }
};
