import { LessonSelect } from "./step.types";

export const REQUEST_COURSES = "REQUEST_COURSES";
export const SUCCESS_COURSES = "SUCCESS_COURSES";
export const FAILURE_COURSES = "FAILURE_COURSES";

export interface DefaultCourseAction {
    courses?: ICourse[];
    error?: string;
    loaded?: boolean;
    nextLoad: number;
}

export interface ICourse {
    count: number;
    title: string;
    id: number;
    slug: string;
    content: string;
    teacher: string;
    description: string;
    instrument_id: number;
    instrument_name: string;
    genre_id: number;
    genre_name: string;
    grade: string;
    color: string;
    thumbnails: ThumbnailsInfo;
    course_select: CourseSelect[];
}
type CourseSelect = {
    exercise: LessonSelect[];
    courses: LessonSelect[];
}
type ThumbnailsInfo = {
    thumbnail: string;
    medium: string;
    medium_large: string;
    large: string;
    '1536x1536': string;
    '2048x2048': string;
};

export interface RequestCourseAction {
    type: typeof REQUEST_COURSES;
    loaded?: boolean;
}
export interface SeccessCourseAction {
    type: typeof SUCCESS_COURSES;
    loaded?: boolean;
    courses: ICourse[];
}
export interface FailureCourseAction {
    type: typeof FAILURE_COURSES;
    loaded?: boolean;
    error: string;
}

export type CourseActionTypes =
    | RequestCourseAction
    | SeccessCourseAction
    | FailureCourseAction;
