export const REQUEST_EXERSICES = "REQUEST_EXERSICES";
export const SUCCESS_EXERSICES = "SUCCESS_EXERSICES";
export const FAILURE_EXERSICES = "FAILURE_EXERSICES";

export interface DefaultExersiceAction {
  exersices?: IExersice[];
  error?: string;
  loaded?: boolean;
  nextLoad: number;
}

export interface IExersice {
  id: number;
  title: string;
  content: string;
  author: string;
  excerpt: string;
  featured_img: string;
  slug: string;
  info: ExersiceInfo;
}
type ExersiceInfo = {
  series: string[];
  instrument: string;
  grade: string[];
  genre: string[];
  img: string[];
  tags: string[];
};

export interface RequestExersiceAction {
  type: typeof REQUEST_EXERSICES;
  loaded?: boolean;
}
export interface SeccessExersiceAction {
  type: typeof SUCCESS_EXERSICES;
  loaded?: boolean;
  exersices: IExersice[];
}
export interface FailureExersiceAction {
  type: typeof FAILURE_EXERSICES;
  loaded?: boolean;
  error: string;
}

export type ExersiceActionTypes =
  | RequestExersiceAction
  | SeccessExersiceAction
  | FailureExersiceAction;
