import React from "react";
import { connect } from "react-redux";
import {
  Avatar,
  CssBaseline,
  TextField,
  Box,
  Button,
  Container,
  Link,
  Grid,
  Typography,
} from "@material-ui/core";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import { Link as RouterLink } from "react-router-dom";
import * as Yup from "yup";
import { Formik } from "formik";
import { loginRequest, logoutRequest } from "../../_actions/user.actions";
import { bindActionCreators } from "redux";
import { AppState } from "../../_store/store.config";
import { ThunkDispatch } from "redux-thunk";
import { AppActions } from "../../_types/app.types";

interface LoginPageProps {
  loading: boolean;
}

type UserLogin = {
  username: string;
  password: string;
};

type Props = LoginPageProps & LinkDispatchProps;

class LoginPage extends React.Component<Props> {
  formSubmit = (e: UserLogin): void => {
    if (e.username && e.password) {
      this.props.login({ username: e.username, password: e.password });
    }
  };

  render() {
    return (
      <Container component="main" maxWidth="xs" style={{ paddingTop: "10px" }}>
        <CssBaseline />
        <div style={{ textAlign: "center" }}>
          <Avatar style={{ margin: "auto" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Sign in
          </Typography>
          <Formik
            initialValues={{
              username: "",
              password: "",
            }}
            validationSchema={Yup.object().shape({
              username: Yup.string()
                .max(255)
                .required("Email or Username is required"),
              password: Yup.string().max(255).required("Password is required"),
            })}
            onSubmit={(event: UserLogin) => {
              this.formSubmit(event);
            }}
          >
            {({
              errors,
              handleBlur,
              handleChange,
              handleSubmit,
              isSubmitting,
              touched,
            }) => (
              <form onSubmit={handleSubmit}>
                <Box mb={3}>
                  <Typography
                    color="textSecondary"
                    align="center"
                    gutterBottom
                    variant="body2"
                  >
                    Sign in on the internal platform
                  </Typography>
                </Box>
                <Grid item>
                  {/* <Button
                    fullWidth
                    onClick={handleSubmit}
                    size="large"
                    variant="contained"
                  >
                    Login with Google{" "}
                  </Button> */}
                </Grid>
                <Box mt={3} mb={1}>
                  <Typography
                    align="center"
                    color="textSecondary"
                    variant="body1"
                  >
                    or login with username / email address{" "}
                  </Typography>
                </Box>
                <TextField
                  color="secondary"
                  error={Boolean(touched.username && errors.username)}
                  fullWidth
                  helperText={touched.username && errors.username}
                  label="Email Address/Username"
                  margin="normal"
                  name="username"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="username"
                  variant="outlined"
                />
                <TextField
                  color="secondary"
                  error={Boolean(touched.password && errors.password)}
                  fullWidth
                  helperText={touched.password && errors.password}
                  label="Password"
                  margin="normal"
                  name="password"
                  onBlur={handleBlur}
                  onChange={handleChange}
                  type="password"
                  variant="outlined"
                />
                <Box my={2}>
                  <Button
                    color="secondary"
                    disabled={isSubmitting}
                    fullWidth
                    size="large"
                    type="submit"
                    variant="contained"
                  >
                    Sign in
                  </Button>
                </Box>
                <Grid container>
                  <Grid item xs>
                    <Link
                      color="secondary"
                      component={RouterLink}
                      to="/lostpassword"
                      variant="body2"
                    >
                      Forgot password?
                    </Link>
                  </Grid>
                  <Grid item>
                    <Typography color="textSecondary" variant="body2">
                      Don&apos;t have an account?{" "}
                      <Link
                        color="secondary"
                        component={RouterLink}
                        to="/signup"
                        variant="body2"
                      >
                        Sign up{" "}
                      </Link>
                    </Typography>
                  </Grid>
                </Grid>
              </form>
            )}
          </Formik>
        </div>
      </Container>
    );
  }
}

interface LinkDispatchProps {
  login: (user: object) => void;
  logout: () => void;
}

const mapStateToProps = (state: AppState, ownProps: Props): LoginPageProps => {
  const { loading } = state.auth;
  return { loading };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>,
  ownProps: LoginPageProps
): LinkDispatchProps => ({
  login: bindActionCreators(loginRequest, dispatch),
  logout: bindActionCreators(logoutRequest, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
