import { authHeader } from "../_helpers/auth-header";
import { apiUrl } from "../_helpers/constants";
import { handleResponse } from "../_helpers/response";

export const getAllExersice = () => {
  const requestOptions: object = {
    method: "GET",
    headers: authHeader(),
  };
  return fetch(`${apiUrl}/lesson/v2?steps=125`, requestOptions).then(handleResponse);
};
export const getSingleExersice = (id: number) => {
  const requestOptions: object = {
    method: "GET",
    headers: authHeader(),
  };
  return fetch(`${apiUrl}/lesson/v2?lesson=${id}`, requestOptions).then(
    handleResponse
  );
};
