export const REQUEST_LESSONS = "REQUEST_LESSONS";
export const SUCCESS_LESSONS = "SUCCESS_LESSONS";
export const FAILURE_LESSONS = "FAILURE_LESSONS";

export interface DefaultLessonAction {
  lessons?: ILesson[];
  error?: string;
  loaded?: boolean;
  nextLoad: number;
}

export interface ILesson {
  id: number;
  title: string;
  content: string;
  author: string;
  excerpt: string;
  featured_img: string;
  slug: string;
  info: LessonInfo;
}
type LessonInfo = {
  series: string[];
  instrument: string;
  grade: string[];
  genre: string[];
  img: string[];
  tags: string[];
};

export interface RequestLessonAction {
  type: typeof REQUEST_LESSONS;
  loaded?: boolean;
}
export interface SeccessLessonAction {
  type: typeof SUCCESS_LESSONS;
  loaded?: boolean;
  lessons: ILesson[];
}
export interface FailureLessonAction {
  type: typeof FAILURE_LESSONS;
  loaded?: boolean;
  error: string;
}

export type LessonActionTypes =
  | RequestLessonAction
  | SeccessLessonAction
  | FailureLessonAction;
