import React, { useState } from "react";
import { Router } from "react-router-dom";
import { SnackbarProvider } from "notistack";
import jssRTL from "jss-rtl";
import { create } from "jss";

import AppRouter from "./router";

import { history } from "./_helpers/history";
import "./App.css";
import {
  Box,
  createStyles,
  jssPreset,
  makeStyles,
  responsiveFontSizes,
  StylesProvider,
  Theme,
  ThemeProvider,
} from "@material-ui/core";

import { LightTheme, DarkTheme } from "./assets/theme";
import Header from "./components/Layout/Header";
import Notifier from "./notifier/Notifier";
import { localTheme } from "./_helpers/constants";

const jss = create({ plugins: [...jssPreset().plugins, jssRTL()] });

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    app: {
      width: "100vw",
      height: "100vh",
      overflowX: "hidden",
      display: "flex",
      flexDirection: "column",
      overflow: "hidden",
    },
    mainBody: {
      height: "inherit",
      overflowX: "hidden",
      overflowY: "auto",
    },
  })
);

const App = () => {
  const [isDark, setIsDark] = useState<boolean>(localTheme("get"));
  const classes = useStyles();

  const theme = responsiveFontSizes(isDark ? DarkTheme : LightTheme);

  return (
    <StylesProvider jss={jss}>
      <ThemeProvider theme={theme}>
        <SnackbarProvider>
          <Router history={history}>
            <Box className={classes.app}>
              <Header theme={{ isDark, setIsDark }} />
              <Notifier />
              <Box className={classes.mainBody}>
                <AppRouter />
              </Box>
            </Box>
          </Router>
        </SnackbarProvider>
      </ThemeProvider>
    </StylesProvider>
  );
};
export default App;
