export const REQUEST_STEPS = "REQUEST_STEPS";
export const SUCCESS_STEPS = "SUCCESS_STEPS";
export const FAILURE_STEPS = "FAILURE_STEPS";

export interface DefaultStepAction {
    steps?: IStep[];
    error?: string;
    loaded?: boolean;
    nextLoad: number;
}

export interface IStep {
    count: number;
    title: string;
    id: number;
    slug: string;
    content: string;
    teacher: string;
    description: string;
    instrument_id: number,
    instrument_name: string,
    genre_id: number,
    genre_name: string,
    grade: string,
    color: string,
    thumbnails: ThumbnailsInfo;
    steps_select: LessonSelect[];
}
export type LessonSelect = {
    title: string
    id: number,
    grade: string,
    author: string,
    content: string
    excerpt: string,
    slug: string,
    featured_img: string
}

type ThumbnailsInfo = {
    thumbnail: string;
    medium: string;
    medium_large: string;
    large: string;
    '1536x1536': string;
    '2048x2048': string;
};

export interface RequestStepAction {
    type: typeof REQUEST_STEPS;
    loaded?: boolean;
}
export interface SeccessStepAction {
    type: typeof SUCCESS_STEPS;
    loaded?: boolean;
    steps: IStep[];
}
export interface FailureStepAction {
    type: typeof FAILURE_STEPS;
    loaded?: boolean;
    error: string;
}

export type StepActionTypes =
    | RequestStepAction
    | SeccessStepAction
    | FailureStepAction;
