import { createStore, combineReducers, applyMiddleware, compose } from "redux";
import thunk, { ThunkMiddleware } from "redux-thunk";
import { postReducer } from "../_reducers/post.reducer";
import { authReducer } from "../_reducers/auth.reducer";
import { AppActions } from "../_types/app.types";
import { snackbarReducer } from "../_reducers/snackbar.reducer";
import { profileReducer } from "../_reducers/profile.reducer";
import { lessonReducer } from "../_reducers/lesson.reducer";
import { stepReducer } from "../_reducers/step.reducer";
import { courseReducer } from "../_reducers/course.reducer";
import { exersiceReducer } from "../_reducers/exersice.reducer";
import { favoriteReducer } from "../_reducers/favorite.reducer";

export const rootReducer = combineReducers({
  snackbar: snackbarReducer,
  posts: postReducer,
  lessons: lessonReducer,
  favorites: favoriteReducer,
  exersices: exersiceReducer,
  steps: stepReducer,
  courses: courseReducer,
  auth: authReducer,
  profile: profileReducer,
});

export type AppState = ReturnType<typeof rootReducer>;

const composeEnhancers =
  (window as any).__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const enhancer = composeEnhancers(
  applyMiddleware(thunk as ThunkMiddleware<AppState, AppActions>)
);

export const store = createStore(rootReducer, enhancer);
