import React from "react";
import { CircularProgress, Container, Grid } from "@material-ui/core";
import { connect } from "react-redux";
import { profileRequest } from "../../_actions/profile.actions";
import { DefaultProfileAction } from "../../_types/profile.types";
import { DefaultUserAction } from "../../_types/user.types";
import { AppState } from "../../_store/store.config";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { AppActions } from "../../_types/app.types";
import LatestSongs from "./Dashboard/LatestSongs";
import TotalCustomers from "./Dashboard/TotalCustomers";
import InstrumentTraffic from "./Dashboard/InstrumentTraffic";
import TotalLevel from "./Dashboard/TotalLevel";
import Budget from "./Dashboard/Budget";
import TotalProfit from "./Dashboard/TotalProfit";

interface ProfilePageProps {
  auth: DefaultUserAction;
  profile: DefaultProfileAction;
}

type Props = ProfilePageProps & LinkDispatchProps;

class ProfilePage extends React.Component<Props> {
  componentDidMount() {
    const token = this.props.auth.user?.token;
    if (token) this.props.getProfile(token);
  }

  render() {
    const { user } = this.props.auth;
    const { profile, loading } = this.props.profile;
    if (loading) {
      return <CircularProgress color="secondary" />;
    }
    if (!profile) {
      return <div>not signed...</div>;
    } else {
      return (
        <Container maxWidth={false} style={{ paddingTop: "10px" }}>
          <Grid container spacing={3}>
            <Grid item lg={12} md={12} xl={12} xs={12}>
              <LatestSongs res={profile.meta.playtime} />
            </Grid>
            {/* <Grid item lg={4} md={12} xl={4} xs={12}>
              <InstrumentTraffic />
            </Grid>
            <Grid
              container
              spacing={3}
              item
              lg={8}
              md={12}
              xl={8}
              xs={12}
              style={{ margin: 0, padding: 0 }}
            > */}
            {/* <Grid item lg={6} md={6} xl={6} xs={12}>
                <TotalLevel />
              </Grid>
              <Grid item lg={6} md={6} xl={6} xs={12}>
                <Budget  />
              </Grid>
              <Grid item lg={6} md={6} xl={6} xs={12}>
                <TotalCustomers  />
              </Grid>
              <Grid item lg={6} md={6} xl={6} xs={12}>
                <TotalProfit /> */}
            {/* </Grid> */}
            {/* </Grid> */}
          </Grid>
        </Container>
      );
    }
  }
}

interface LinkDispatchProps {
  getProfile: (token: string) => void;
}

const mapStateToProps = (
  state: AppState,
  ownProps: Props
): ProfilePageProps => {
  const { profile, auth } = state;
  return { profile, auth };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>,
  ownProps: ProfilePageProps
): LinkDispatchProps => ({
  getProfile: bindActionCreators(profileRequest, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(ProfilePage);
