import React, { Dispatch, SetStateAction, useReducer } from "react";
import MainAppBar from "./AppBar/MainAppBar";
import { ThunkDispatch } from "redux-thunk";
import { AppActions } from "../../_types/app.types";
import { logoutRequest } from "../../_actions/user.actions";
import { bindActionCreators } from "redux";
import { AppState } from "../../_store/store.config";
import { connect } from "react-redux";

export interface HeaderState {
  menuId: string;
  menuIsOpen: boolean;
  mobileMenuId: string;
  profileMoreAnchor: null | HTMLElement;
  mobileMoreAnchor: null | HTMLElement;
}

const initialState: HeaderState = {
  menuId: "primary-search-account-menu",
  mobileMenuId: "primary-search-account-menu-mobile",
  profileMoreAnchor: null,
  mobileMoreAnchor: null,
  menuIsOpen: false,
};

export type HeaderAction =
  | { type: "menu-toggle" | "profile-close" | "mobile-close" }
  | { type: "field"; fieldName: string; payload: string }
  | { type: "loggedIn"; payload: boolean }
  | { type: "profile-anchor"; payload: null | HTMLElement }
  | { type: "mobile-anchor"; payload: null | HTMLElement };

function HeaderReducer(state: HeaderState, action: HeaderAction) {
  switch (action.type) {
    case "field": {
      return {
        ...state,
        [action.fieldName]: action.payload,
      };
    }
    case "menu-toggle": {
      return {
        ...state,
        menuIsOpen: !state.menuIsOpen,
      };
    }
    case "profile-close": {
      return {
        ...state,
        profileMoreAnchor: null,
      };
    }
    case "mobile-close": {
      return {
        ...state,
        mobileMoreAnchor: null,
      };
    }
    case "mobile-anchor": {
      return {
        ...state,
        mobileMoreAnchor: action.payload,
      };
    }
    case "profile-anchor": {
      return {
        ...state,
        profileMoreAnchor: action.payload,
      };
    }
    default:
      return state;
  }
}

interface HeaderProps {
  theme: {
    isDark: boolean;
    setIsDark: Dispatch<SetStateAction<boolean>>;
  };
}

export type IHeaderProps = HeaderProps & LinkDispatchProps & MapProps;

const Header: React.FC<IHeaderProps> = ({ ...rest }) => {
  const [state, dispatch] = useReducer(HeaderReducer, initialState);

  return <MainAppBar state={state} dispatch={dispatch} {...rest} />;
};

interface LinkDispatchProps {
  logout?: () => void;
}
interface MapProps {
  loggedIn: boolean;
}

const mapStateToProps = (state: AppState): MapProps => {
  const { loggedIn } = state.auth;
  return { loggedIn };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  logout: bindActionCreators(logoutRequest, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(Header);
