import { getTheTime } from "../_helpers/constants";
import {
  StepActionTypes,
  REQUEST_STEPS,
  SUCCESS_STEPS,
  FAILURE_STEPS,
  DefaultStepAction,
} from "../_types/step.types";

const stepReducerDefaultState: DefaultStepAction = {
  loaded: false,
  nextLoad: getTheTime(),
};

export const stepReducer = (
  state = stepReducerDefaultState,
  action: StepActionTypes
): DefaultStepAction => {
  switch (action.type) {
    case REQUEST_STEPS:
      return state;
    case SUCCESS_STEPS:
      return {
        steps: action.steps,
        nextLoad: getTheTime(2),
        loaded: true,
      };
    case FAILURE_STEPS:
      return {
        error: action.error,
        nextLoad: getTheTime(),
        loaded: true,
      };
    default:
      return state;
  }
};
