import { Dispatch } from "react";
import {
  FAILURE_COURSES,
  REQUEST_COURSES,
  SUCCESS_COURSES,
  ICourse,
} from "../_types/course.types";
import { getAllCourse } from "../_services/course.service";
import { AppState } from "../_store/store.config";
import { SHOW_SNACKBAR } from "../_types/snackbar.types";
import { AppActions } from "../_types/app.types";
import { getTheTime } from "../_helpers/constants";
import { snackBarMaker } from "./snackbar.actions";

export const requestAllCourse = () => {
  return (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const courseState = getState().courses;
    const currentTime = getTheTime();
    const resetTime = currentTime - courseState.nextLoad;
    if (courseState.loaded && resetTime <= 0) {
      return;
    }
    dispatch({ type: REQUEST_COURSES, loaded: false });

    getAllCourse().then(
      (courses: ICourse[]) => dispatch({ type: SUCCESS_COURSES, courses }),
      (errorObj: object) => {
        const error: string = errorObj.toString();
        dispatch({ type: FAILURE_COURSES, error });
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker(error, "error"),
        });
      }
    );
  };
};
