import { getTheTime } from "../_helpers/constants";
import {
  ExersiceActionTypes,
  REQUEST_EXERSICES,
  SUCCESS_EXERSICES,
  FAILURE_EXERSICES,
  DefaultExersiceAction,
} from "../_types/exersice.types";

const exersiceReducerDefaultState: DefaultExersiceAction = {
  loaded: false,
  nextLoad: getTheTime(),
};

export const exersiceReducer = (
  state = exersiceReducerDefaultState,
  action: ExersiceActionTypes
): DefaultExersiceAction => {
  switch (action.type) {
    case REQUEST_EXERSICES:
      return state;
    case SUCCESS_EXERSICES:
      return {
        exersices: action.exersices,
        nextLoad: getTheTime(2),
        loaded: true,
      };
    case FAILURE_EXERSICES:
      return {
        error: action.error,
        nextLoad: getTheTime(),
        loaded: true,
      };
    default:
      return state;
  }
};
