import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";

import { AppActions } from "../../../_types/app.types";
import { ICourse } from "../../../_types/course.types";
import { AppState } from "../../../_store/store.config";
import { requestAllCourse } from "../../../_actions/course.actions";
import { CourseCard } from "./CourseCard";
import {
  Chip,
  CircularProgress,
  Container,
  Grid,
  Typography,
} from "@material-ui/core";

interface CourseProps {
  courses?: ICourse[];
  error?: string;
  loaded?: boolean;
}

type Props = CourseProps & LinkDispatchProps;

class CourseList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllCourse();
  }

  render() {
    const { loaded, courses, error } = this.props;
    const length = courses ? courses.length : 0;
    return (
      <Container maxWidth={false} style={{ position: "relative" }}>
        <Chip
          color="secondary"
          label={"تعداد: " + length}
          style={{ position: "absolute", left: 0, top: 0 }}
        />
        <Typography variant="h3">مجموعه‌ها</Typography>
        <Grid container justify="center" spacing={2}>
          {loaded ? (
            error ? (
              <Typography component="body">{error}</Typography>
            ) : (
              courses?.map((course: ICourse) => (
                <CourseCard key={course.id} course={course} />
              ))
            )
          ) : (
            <CircularProgress color="secondary" />
          )}
        </Grid>
      </Container>
    );
  }
}

interface LinkDispatchProps {
  requestAllCourse: () => void;
}

const mapStateToProps = (state: AppState): CourseProps => {
  const { error, courses, loaded } = state.courses;
  return {
    courses,
    error,
    loaded,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllCourse: bindActionCreators(requestAllCourse, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(CourseList);
