import { Dispatch } from "react";
import {
  FAILURE_LESSONS,
  REQUEST_LESSONS,
  SUCCESS_LESSONS,
  ILesson,
} from "../_types/lesson.types";
import { getAllLesson } from "../_services/lesson.service";
import { AppState } from "../_store/store.config";
import { SHOW_SNACKBAR } from "../_types/snackbar.types";
import { AppActions } from "../_types/app.types";
import { getTheTime } from "../_helpers/constants";
import { snackBarMaker } from "./snackbar.actions";

export const requestAllLesson = () => {
  return (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const lessonState = getState().lessons;
    const currentTime = getTheTime();
    const resetTime = currentTime - lessonState.nextLoad;
    if (lessonState.loaded && resetTime <= 0) {
      return;
    }
    dispatch({ type: REQUEST_LESSONS, loaded: false });

    getAllLesson().then(
      (lessons: ILesson[]) => dispatch({ type: SUCCESS_LESSONS, lessons }),
      (errorObj: object) => {
        const error: string = errorObj.toString();
        dispatch({ type: FAILURE_LESSONS, error });
        dispatch({
          type: SHOW_SNACKBAR,
          snackbar: snackBarMaker(error, "error"),
        });
      }
    );
  };
};
