import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { connect } from "react-redux";

import { AppActions } from "../../../_types/app.types";
import { IStep } from "../../../_types/step.types";
import { AppState } from "../../../_store/store.config";
import { requestAllStep } from "../../../_actions/step.actions";
import { StepCard } from "./StepCard";
import {
  Chip,
  Grid,
  Typography,
  Container,
  CircularProgress,
} from "@material-ui/core";

interface StepProps {
  steps?: IStep[];
  error?: string;
  loaded?: boolean;
}

type Props = StepProps & LinkDispatchProps;

class StepList extends Component<Props> {
  componentDidMount() {
    this.props.requestAllStep();
  }

  render() {
    const { loaded, steps, error } = this.props;
    const length = steps ? steps.length : 0;
    return (
      <Container maxWidth={false} style={{ position: "relative" }}>
        <Chip
          color="secondary"
          label={"تعداد: " + length}
          style={{ position: "absolute", left: 0, top: 0 }}
        />
        <Typography variant="h3">آموزش ها</Typography>
        <Grid container justify="center" spacing={2}>
          {loaded ? (
            error ? (
              <Typography component="body">{error}</Typography>
            ) : (
              steps?.map((step: IStep) => (
                <StepCard key={step.id} step={step} />
              ))
            )
          ) : (
            <CircularProgress color="secondary" />
          )}
        </Grid>
      </Container>
    );
  }
}

interface LinkDispatchProps {
  requestAllStep: () => void;
}

const mapStateToProps = (state: AppState): StepProps => {
  const { error, steps, loaded } = state.steps;
  return {
    steps,
    error,
    loaded,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  requestAllStep: bindActionCreators(requestAllStep, dispatch),
});

export default connect(mapStateToProps, mapDispatchToProps)(StepList);
