import { authHeader } from "../_helpers/auth-header";
import { apiUrl } from "../_helpers/constants";
import { handleResponse } from "../_helpers/response";

export const userLogin = (user: object) => {
  console.log("called");

  const requestOptions: RequestInit = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(user),
  };
  return fetch(`${apiUrl}/jwt-auth/v1/token`, requestOptions)
    .then(handleResponse)
    .then((user) => {
      // store user details and jwt token in local storage to keep user logged in between page refreshes
      localStorage.setItem("user", JSON.stringify(user));
      return user;
    });
};
export const userLogout = () => {
  // remove user from local storage to log user out
  localStorage.removeItem("user");
};

export const userRegister = (user: object) => {
  const requestOptions = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(user),
  };

  return fetch(`${apiUrl}/jwt-auth/v1/register`, requestOptions).then(
    handleResponse
  );
};
export const userLost = (user: object) => {
  const requestOptions: RequestInit = {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(user),
  };
  return fetch(`${apiUrl}/jwt-auth/v1/lostpassword`, requestOptions).then(
    handleResponse
  );
};
export const userUpdate = (user: any) => {
  const requestOptions: RequestInit = {
    method: "PUT",
    headers: { ...authHeader(), "Content-Type": "application/json" },
    body: JSON.stringify(user),
  };
  return fetch(`${apiUrl}/wp/v2/users/${user.id}`, requestOptions).then(
    handleResponse
  );
};
