import { getTheTime } from "../_helpers/constants";
import {
  PostActionTypes,
  REQUEST_POSTS,
  SUCCESS_POSTS,
  FAILURE_POSTS,
  DefaultPostAction,
} from "../_types/post.types";

const postReducerDefaultState: DefaultPostAction = {
  loaded: false,
  nextLoad: getTheTime(),
};

export const postReducer = (
  state = postReducerDefaultState,
  action: PostActionTypes
): DefaultPostAction => {
  switch (action.type) {
    case REQUEST_POSTS:
      return state;
    case SUCCESS_POSTS:
      return {
        posts: action.posts,
        nextLoad: getTheTime(10),
        loaded: true,
      };
    case FAILURE_POSTS:
      return {
        error: action.error,
        nextLoad: getTheTime(),
        loaded: true,
      };
    default:
      return state;
  }
};
