import { getTheTime } from "../_helpers/constants";
import {
  CourseActionTypes,
  REQUEST_COURSES,
  SUCCESS_COURSES,
  FAILURE_COURSES,
  DefaultCourseAction,
} from "../_types/course.types";

const courseReducerDefaultState: DefaultCourseAction = {
  loaded: false,
  nextLoad: getTheTime(),
};

export const courseReducer = (
  state = courseReducerDefaultState,
  action: CourseActionTypes
): DefaultCourseAction => {
  switch (action.type) {
    case REQUEST_COURSES:
      return state;
    case SUCCESS_COURSES:
      return {
        courses: action.courses,
        nextLoad: getTheTime(2),
        loaded: true,
      };
    case FAILURE_COURSES:
      return {
        error: action.error,
        nextLoad: getTheTime(),
        loaded: true,
      };
    default:
      return state;
  }
};
