export const REQUEST_FAVORITES = "REQUEST_FAVORITES";
export const SUCCESS_FAVORITES = "SUCCESS_FAVORITES";
export const FAILURE_FAVORITES = "FAILURE_FAVORITES";

export interface DefaultFavoriteAction {
  favorites?: IFavorite[];
  error?: string;
  loaded?: boolean;
  nextLoad: number;
}

export interface IFavorite {
  id: number;
  title: string;
  content: string;
  author: string;
  excerpt: string;
  featured_img: string;
  slug: string;
  info: FavoriteInfo;
}
type FavoriteInfo = {
  series: string[];
  instrument: string;
  grade: string[];
  genre: string[];
  img: string[];
  tags: string[];
};

export interface RequestFavoriteAction {
  type: typeof REQUEST_FAVORITES;
  loaded?: boolean;
}
export interface SeccessFavoriteAction {
  type: typeof SUCCESS_FAVORITES;
  loaded?: boolean;
  favorites: IFavorite[];
}
export interface FailureFavoriteAction {
  type: typeof FAILURE_FAVORITES;
  loaded?: boolean;
  error: string;
}

export type FavoriteActionTypes =
  | RequestFavoriteAction
  | SeccessFavoriteAction
  | FailureFavoriteAction;
