import React from "react";
import {
  SHOW_SNACKBAR,
  REMOVE_SNACKBAR,
  DefaultSnackAction,
  CLEAR_SNACKBAR,
  ISnack,
} from "../_types/snackbar.types";
import { IconButton } from "@material-ui/core";
import { store } from "../_store/store.config";
import { UniqId } from "../_helpers/constants";
import { Close } from "@material-ui/icons";

export const snackbarShow = (snackbar: DefaultSnackAction) => {
  return {
    type: SHOW_SNACKBAR,
    snackbar: {
      ...snackbar,
    },
  };
};
export const snackbarRemove = (key: string) => {
  return { type: REMOVE_SNACKBAR, key };
};

export const snackBarMaker = (
  message: string,
  variant: "error" | "success" | "warning"
): ISnack => {
  return {
    message: message,
    key: UniqId(),
    options: {
      variant: variant,
      action: (key: string) => (
        <IconButton
          style={{ borderRadius: 5 }}
          color="inherit"
          size="small"
          aria-label="close snackbar"
          component="span"
          onClick={() =>
            store.dispatch({
              type: CLEAR_SNACKBAR,
              dismissAll: !key,
              key,
            })
          }
        >
          <Close />
        </IconButton>
      ),
    },
  };
};
