import {
  // ADD_REQUEST,
  // ADD_SUCCESS,
  // ADD_FAILURE,
  INFO_REQUEST,
  INFO_FAILURE,
  INFO_SUCCESS,
  // REMOVE_REQUEST,
  // REMOVE_SUCCESS,
  // REMOVE_FAILURE,
  ProfileActionTypes,
  SAVE_PROFILE,
  DefaultProfileAction,
} from "../_types/profile.types";

const profileReducerDefaultState: DefaultProfileAction = {
  loading: true,
};

export function profileReducer(
  state = profileReducerDefaultState,
  action: ProfileActionTypes
): DefaultProfileAction {
  switch (action.type) {
    case SAVE_PROFILE:
      return {
        loading: true,
        profile: action.userData,
      };
    case INFO_REQUEST:
      return state;
    case INFO_SUCCESS:
      return {
        loading: false,
        profile: action.profile,
      };
    case INFO_FAILURE:
      return {
        loading: false,
        error: action.error,
      };
    default:
      return state;
  }
}
