import React from "react";
import { ICourse } from "../../../_types/course.types";
import { createStyles, makeStyles, Theme } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import Button from "@material-ui/core/Button";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import CardActionArea from "@material-ui/core/CardActionArea";
import { thumbnailsFinder } from "../../../_helpers/constants";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      maxWidth: 345,
      margin: theme.spacing(2),
    },
    cardAction: {
      justifyContent: "space-between",
      padding: "10px 20px",
    },
    content: {
      minHeight: "140px",
    },
  })
);

type Props = {
  course: ICourse;
};
export const CourseCard: React.FC<Props> = ({ course }) => {
  const classes = useStyles();
  const { title, thumbnails, description, teacher } = course;
  return (
    <Card className={classes.root}>
      <CardActionArea>
        <CardMedia
          component="img"
          alt="Contemplative Reptile"
          height="140"
          image={thumbnailsFinder(thumbnails)}
          title="Contemplative Reptile"
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="h5" component="h2">
            {title}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {description}
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions className={classes.cardAction}>
        <Typography variant="body2" color="textSecondary" component="p">
          از {teacher}
        </Typography>
        <Button size="medium" color="secondary">
          ادامه مطلب
        </Button>
      </CardActions>
    </Card>
  );
};
